import React , { Component } from 'react'
import GoogleMapReact from 'google-map-react'
import {GoogleApiWrapper} from 'google-maps-react';
import { connect } from 'react-redux';
import thunk from 'redux-thunk';
import { Anchor, Box, Button, Grommet, Layer } from 'grommet'

import FridgeModal from './UI/FridgeModal';
import LocationPin from './LocationPin';
import './map.css'

import * as actions from '../redux/actions/fridge';


class Map extends Component {

  state = {
    modal: null
  }
  onModalOpen = (id) =>  {
    this.props.getFridge(id)
    this.setState({modal: id});
  }
  modalClosedHandler = () => {
    this.setState({
      modal: null
    });
  }

  render () {
    let f = this.props.currentFridge;
    let modalInfo = null;
    if (f) {
      let status;
      if (f.confirmed === true) {
        status = (<h4>Status: Confirmed</h4>)}
        else {
          console.log('currentFridge ID', f.id)
          status= (<div><h4>Status: Unconfirmed</h4><p>Please only confirm fridges if you know they are active and listed at the correct location. Thanks!</p><Button primary active onClick={() => this.props.onFridgeConfirmed(f)} label="Confirm Fridge" /></div>)
        }

      modalInfo = (
        <div>
        <h2>{f.name}</h2>
        <h2>Neighborhood: {f.neighborhood}</h2>
        <h3>Location: {f.streetAddress}</h3>
        <h3>Notes: {f.notes}</h3>
        <Button href={f.link} label="Social Media" />
        {status}
        </div>
      );
    }
    return (
      <div className="map">
    <div className="google-map">
      <FridgeModal
        display={this.state.modal}
        modalClosed={this.modalClosedHandler}
        fridgeData={f}
      >
        {modalInfo}
      </FridgeModal>
      <GoogleMapReact
        
        bootstrapURLKeys={{ key: process.env.REACT_APP_AUTH_TOKEN }}
        defaultCenter={this.props.location}
        defaultZoom={this.props.zoomLevel}
      >
        {this.props.fridges.map(f => (
          <LocationPin
          lat={f.lat}
          lng={f.lng}
          fridgeData={f}
          toggleHover={this.props.toggleHover}
          hover={this.props.hover}
          onModalOpen={this.onModalOpen}
        />
        ))}
      </GoogleMapReact>
    </div>
  </div>
    )}
}

const mapStateToProps = state => {
  return {
    currentFridge: state.currentFridge
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getFridge: (fridgeID) => dispatch(actions.getFridge(fridgeID)),
    onFridgeConfirmed: (fridge) => dispatch(actions.confirmFridge(fridge))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Map);